<template>
    <v-app>
      <v-toolbar flat color="white" max-height="70">
        <v-toolbar-title>Crud Atma Pedia</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-dialog v-model="dialog" max-width="500px">
          <template v-slot:activator="{ on }">
            <v-btn color="primary" dark class="mb-2" v-on="on">Buat Minuman</v-btn>
          </template>
          <v-card>
            <v-card-title>
              <span class="headline">{{ formTitle }}</span>
            </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.name" label="Drinks name"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-overflow-btn v-model="editedItem.calories" :items="items" label="Calories" target="#dropdown-example">
                    </v-overflow-btn>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.fat" label="Fat (g)"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.carbs" label="Carbs (g)"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.protein" label="Protein (g)"></v-text-field>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
              <v-btn color="blue darken-1" text @click="save">Save</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    <v-container class="my-5">
      <v-layout row wrap>
        <v-flex md6 xs12 sm6 lg3 v-for="drink in drinks" :key="drink['.key']">
          <v-card class="mx-2 my-2" max-width="344">
            <v-list-item three-line>
              <v-list-item-content>
                <div class="overline mb-4">Minuman</div>
                <v-list-item-title class="headline mb-1">{{drink.name}}</v-list-item-title>
                <v-list-item-subtitle>
                  <table>
                    <tr>
                      <td>Calories</td>
                      <td>:</td>
                      <td>{{drink.calories}}</td>
                    </tr>
                    <tr>
                      <td>Fat</td>
                      <td>:</td>
                      <td>{{drink.fat}}</td>
                    </tr>
                    <tr>
                      <td>Carbs</td>
                      <td>:</td>
                      <td>{{drink.carbs}}</td>
                    </tr>
                    <tr>
                      <td>Protein</td>
                      <td>:</td>
                      <td>{{drink.protein}}</td>
                    </tr>
                  </table>
                </v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>
            <v-card-actions>
              <v-btn @click="editItem(drink)" color="success">Edit</v-btn>
              <v-btn @click="deleteItem(drink)" color="error">Delete</v-btn>
            </v-card-actions>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </v-app>
</template>

<script>
  import { drinksRef } from'../firebase';
  export default {
    data: () => ({
      items: ['ES', 'Hangat'],
      cek : -1,
      dialog: false,
      search:'',
      headers: [
        {
          text: 'Drinks (100g serving)',
          align: 'left',
          sortable: false,
          value: 'name',
        },
        { text: 'Calories', value: 'calories' },
        { text: 'Fat (g)', value: 'fat' },
        { text: 'Carbs (g)', value: 'carbs' },
        { text: 'Protein (g)', value: 'protein' },
        { text: 'Actions', value: 'action', sortable: false },
      ],
      drinks: [],
      editedIndex: -1,
      editedItem: {
        name: '',
        calories: '',
        fat: 0,
        carbs: 0,
        protein: 0,
      },
      defaultItem: {
        name: '',
        calories: '',
        fat: 0,
        carbs: 0,
        protein: 0,
      },
    }),
    firebase:{
      drinks : drinksRef
    },
    computed: {
      formTitle () {
        return this.cek === -1 ? 'New Item' : 'Edit Item'
      },
    },
    watch: {
      dialog (val) {
        val || this.close()
      },
    },
    methods: {
      editItem (item) {
        this.editedIndex = item['.key']
        this.cek = 0
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },
      deleteItem (item) {
        confirm('Are you sure you want to delete this item?') && drinksRef.child((item['.key'])).remove()
      },
      close () {
        this.dialog = false
        setTimeout(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        }, 300)
      },
      save () {
        if (this.cek > -1) {
          drinksRef.child(this.editedIndex).set({
            name: this.editedItem.name,
            calories: this.editedItem.calories,
            fat: this.editedItem.fat,
            carbs: this.editedItem.carbs,
            protein: this.editedItem.protein})
        this.clear()
        } else {
          drinksRef.push(this.editedIndex).set({
            name: this.editedItem.name,
            calories: this.editedItem.calories,
            fat: this.editedItem.fat,
            carbs: this.editedItem.carbs,
            protein: this.editedItem.protein})
        this.clear()
        }
        this.close()
      },
      clear(){
        this.editedItem={}
        this.editedItem.name=''
        this.editedItem.calories=0
        this.editedItem.fat=0
        this.editedItem.carbs=0
        this.editedItem.prtein=0
        this.cek=-1
      },
    },
  }
</script>




